import React, { useState, useCallback, useEffect } from "react";
import Head from "next/head";
import AppLayout from "../components/AppLayout";
import { Form, Input, Checkbox, Button, notification } from "antd";
import useInput from '../hooks/useInput';
import { useDispatch ,useSelector} from 'react-redux';
import { SIGN_UP_REQUEST } from '../reducers/user';
import Router from 'next/router';



const Signup = () => {
    const dispatch = useDispatch();
    const [email, onChangeEmail] = useInput("");
    const [nickname, onChangeNickname] = useInput("");
    const [password, onChangePassword] = useInput("");
    const [passwordCheck, setPasswordCheck] = useState("");
    const [passwordError, setPasswordError] = useState(false);
    const [term, setTerm] = useState(false);
    const [termError, setTermError] = useState(false);
    // const { signUpLoading} = useSelector((state) => state.user);
    const { signUpLoading, me, signUpDone, signUpError} = useSelector((state) => state.user);

    useEffect(() => {
        if (signUpDone) {
            notification.open({
                message: '',
                description: "  !   "
            })
        }
    }, [signUpDone]);

    useEffect(() => {
        if (me && me.id) {
            notification.open({
                message: '',
                description: "    !"
            })
            Router.replace('/');
        }
    }, [me && me.id]);

    useEffect(() => {
        if (signUpError) {
            alert(signUpError);
        }
    }, [signUpError]);

    useEffect(() => {
        if (signUpDone) {
            Router.replace('/');
        }
    }, [signUpDone]);

    
    const onSubmit = useCallback(() => {
        if (password !== passwordCheck) {
            return setPasswordError(true);
        }
        if (!term) {
            return setTermError(true);
        }
        // console.log("   : id, nick, password : ", email, nickname, password);

        return dispatch({
            type: SIGN_UP_REQUEST,
            data: {
                email,
                nickname,
                password,
            },
        });


    }, [email, password, passwordCheck, term]);

    const onChangePasswordCheck = useCallback(
        (e) => {
            setPasswordError(e.target.value !== password);
            setPasswordCheck(e.target.value);
        },
        [password]
    );

    const onChangeTerm = useCallback((e) => {
        setTermError(false);
        setTerm(e.target.checked);
    }, []);


    return (
        <AppLayout>
            <Head>
                <title>  | NodeBird</title>
            </Head>

            <Form onFinish={onSubmit}>
                <div>
                    <label htmlFor="user-id"></label>
                    <br />
                    <Input
                        name="user-id"
                        value={email}
                        required
                        onChange={onChangeEmail}
                    />
                </div>
                <div>
                    <label htmlFor="user-nick"></label>
                    <br />
                    <Input
                        name="user-nick"
                        value={nickname}
                        required
                        onChange={onChangeNickname}
                    />
                </div>
                <div>
                    <label htmlFor="user-password"></label>
                    <br />
                    <Input
                        name="user-password"
                        type="password"
                        value={password}
                        required
                        onChange={onChangePassword}
                    />
                </div>
                <div>
                    <label htmlFor="user-password-check"></label>
                    <br />
                    <Input
                        name="user-password-check"
                        type="password"
                        value={passwordCheck}
                        required
                        onChange={onChangePasswordCheck}
                    />

                    {passwordError && (
                        <div style={{ color: "red" }}>
                              .
                        </div>
                    )}
                </div>

                <div>
                    <Checkbox
                        name="user-term"
                        checked={term}
                        onChange={onChangeTerm}
                    >
                             .
                    </Checkbox>
                    {termError && (
                        <div style={{ color: "red" }}>
                              .
                        </div>
                    )}
                </div>

                <div style={{ marginTop: 10 }}>
                    <Button type="primary" htmlType="submit" loading={signUpLoading}></Button>
                </div>

            </Form>
        </AppLayout>
    );
};


export default Signup;